window.onload = async function () {
    console.log(`dev:onload`);

    // populates BNB Raised bar from firebase
    populateAmountRaisedinHTMLDB();

    /**
     * If web3enabled 
     * - see if user is already connected 
     * - enableWeb3
     * - if user is already connected - but only page was refreshed - login()
     * Else if web3notenabled but loggedIn user
    */

    if (await isWeb3Installed()) {
        console.log(`dev: web3 installed  - checking if user is connected upon refreshing`);
        try {
            console.log("dev: onload - enabling web3");
            await Moralis.enableWeb3();
            console.log("dev: onload - enabled web3");
        } catch (e) {
            console.error(e), console.error("dev: onload - failed to enable web3");
        }

        if (isUserConnected()) {
            console.log("dev: Page-refreshed - User already connected");
            login();
        } else {
            console.log("dev: Page-refreshed - User not connected")
        }
    } else if (!(await isWeb3Installed()) && isUserConnected()) {
        // Shouldn't this show error coz web3 wasn't enabled to check isUserConnected?
        // I'm not sure about the below trycatch block coz I'm using it to enableWeb3 and I think checking isUserConnected requires it to be enabled so commenting it out 
        // try {
        //     console.log("dev: onload - enabling web3-WC");
        //     await Moralis.enableWeb3({ provider: "walletconnect" });
        //     console.log("dev: onload - enabled web3-WC");
        // } catch (e) {
        //     console.error(e);
        //     console.error("dev: onload - failed to enable web3-WC");
        // }
        if (isUserConnected()) {
            console.log("dev: Page-refreshed - User already connected");
            login("walletconnect");
        } else {
            console.log("dev: Page-refreshed - User not connected");
        }
    }
};
