async function login(provider) {

    let BSC_Chain = TEST_ENVIRONMENT ? BscChainIdTest : BscChainIdMain;
    let user = Moralis.User.current();

    if (!user) {
        console.info(`dev: login() - Provider :${provider}`);

        try {
            console.log(`Deactivating old instance of Web3`);
            Moralis.isEnablingWeb3 && await Moralis.deactivateWeb3();
            console.log(`Deactivated old instance of Web3`);
        } catch (e) {
            console.error(e);
            console.error(`dev:failed Deactivating old instance of Web3`);
        }

        if (provider === 'walletconnect') {
            try {
                console.info(`⚪Authenticating with WalletConnect`);
                user = await Moralis.authenticate({
                    provider: "walletconnect",
                    chainId: BSC_Chain,
                    signingMessage: "Battle Infinity Authentication: WalletConnect"
                });
                console.info(`✅Authenticated with WalletConnect`);
            } catch (error) {
                console.error(error);
                console.error(`dev:❌Authentication Failed - Walletconnect - login()`);
            }
        } else {
            console.info(`Authenticating with MetaMask`);
            try {
                currentChainIdHex = await Moralis.getChainId();
                // Authenticate only if user is on correct chain
                if (currentChainIdHex !== BSC_Chain) {
                    console.log(`Current chain -${currentChainIdHex}`);
                    console.log("BSC chain - 56");
                    console.log(`BSC_Chain - ${BSC_Chain}`);

                    try {
                        console.log("dev: login metamask - enabling web3 for switching chain");
                        await Moralis.enableWeb3();
                        console.log("dev: login metamask - enabled web3 for switching chain");
                    } catch (e) {
                        console.error(e);
                        console.error("dev: login metamask - failed to enable web3 for switching chain");
                    }
                    await switchAndAdd(BSC_Chain);
                }

                user = await Moralis.authenticate({ signingMessage: "Battle Infinity Authentication" })

                try {
                    if (!TEST_ENVIRONMENT) hitDataLayer(getUserWalletAddress())
                } catch (error) {
                    console.error(`dev:couldn't hit data layer`);
                }

            } catch (error) {
                console.error(error);
                console.error("dev: login metamask - failed to enable web3 for switching chain");
            }

        }
    }

    try {
        currentChainIdHex = await Moralis.getChainId();
        console.info("dev:getChainId() succeded - login()");
        console.info(`dev: current chain ${currentChainIdHex} - login()`);
    } catch (e) {
        console.error(e);
        console.error("dev:getChainId() Failed - login()");
    }

    if (isUserConnected()) {
        console.log("IBAT Authentication - userconnected");
        atLogin();

    }
}


function getUserWalletAddress() {
    let err = 'Wallet not connected';
    if (!isUserConnected())
        return err;

    return Moralis.User.current().get("ethAddress");
}

function isUserConnected() {
    let user = Moralis.User.current();
    return user ? true : false;
}

