function toggleBtnVisibility(connected) {
    const allBtns = document.querySelectorAll('.wallet-options-btn ');
    if (connected) { //show
        allBtns.forEach(btn => {
            btn.classList.add('hidden');
        });
        logoutBtn && logoutBtn.classList.remove('hidden');
        showTokenBtn && showTokenBtn.classList.remove('hidden');
        // loginBtn && loginBtn.classList.remove('hidden');
    }
    else {
        if (!allBtns) {
            console.error(`ln:249-Invalid Arguments`);
        } else {
            allBtns.forEach(btn => {
                btn.classList.remove('hidden');
            });
        }
        // walletOptions && walletOptions.classList.remove('hidden');
        logoutBtn && logoutBtn.classList.add('hidden');
        showTokenBtn && showTokenBtn.classList.add('hidden');
    }
}

function toggleConnectStatus() {
    const statusFields = document.querySelectorAll('.connectivity-status');
    statusFields && statusFields.forEach(el => {
        if (isUserConnected()) {
            el.classList.remove('bg-red-400')
            el.style.background = "#4ade80";
            el.innerHTML = 'connected'
        } else {
            el.style.background = "#f87171";
            el.innerHTML = 'not connected'
        }
    });
}


function convertTokenBalance(balance, decimals) {
    let newBalance = balance == 0 ? 0 : balance / Math.pow(10, decimals);
    newBalance = newBalance < 0.001 ? 0 : newBalance;
    return newBalance;
}



async function isPresaleOpen() {
    const ABI = [{ "inputs": [], "name": "isPresaleOpen", "outputs": [{ "internalType": "bool", "name": "", "type": "bool" }], "stateMutability": "view", "type": "function" }];
    const result = await readFunction('isPresaleOpen', ABI);
    // console.log(`isPresaleOpen = ${result}`);
    return result;
}

// isPresaleOpen();

async function readFunction(functionName, ABI) {

    let options = {
        contractAddress: DEPLOYED_CONTRACT_ADDRESS,
        functionName: functionName,
        abi: ABI,
    }
    return await Moralis.executeFunction(options);
}



