async function buyToken() {
    const BNBValue = +getBuyAmount();

    const errMsgContainer = document.getElementById('buy-token-error-con');
    const errMsg = document.getElementById('buy-token-error-msg');
    let msg = 'Error';

    if (BNBValue <= 0) {
        msg = 'Enter valid amount between 0.1-500BNB';
        console.error(`dev: BNBAmount = ${BNBValue}`);
        showErrMsg(errMsgContainer, errMsg, msg);
        return;
    }
    if (isNaN(BNBValue)) {
        msg = 'Enter valid amount';
        console.error(`dev: BNBAmount = ${BNBValue}`);
        console.error(`dev: isNaN(BNBAmount) = ${isNaN(BNBValue)}`);
        showErrMsg(errMsgContainer, errMsg, msg);
        return;
    }
    if (!isUserConnected()) {
        console.log('User not Logged in - connect wallet');
        msg = 'Wallet not connected';
        showErrMsg(errMsgContainer, errMsg, msg);
        return;
    }
    let isSaleOpen = false;
    try {
        isSaleOpen = await isPresaleOpen();
        console.log(`sale open - ${isSaleOpen}`);
    } catch (error) {
        console.error(error);
        console.error(`dev:failed isSaleOpen() - buyToken()`);
    }
    if (!isSaleOpen) {
        console.log('Presale is not open');
        msg = 'Presale has not yet started !!!';
        showErrMsg(errMsgContainer, errMsg, msg);
        return;
    }
    const MIN_BNB = TEST_ENVIRONMENT ? 0 : 0.1;
    const MAX_BNB = 500;
    if ("0xc8179e6927b61a4fdc3e5a2db14e641e51b9ad83" !== getUserWalletAddress() && +BNBValue < MIN_BNB) {
        msg = 'Minimum BNB Amount should be 0.1BNB';
        showErrMsg(errMsgContainer, errMsg, msg);
        return;
    }
    if (+BNBValue > MAX_BNB) {
        msg = 'Maximum BNB Amount is 500BNB';
        showErrMsg(errMsgContainer, errMsg, msg);
        return;
    }

    const FUNCTION_NAME = 'buyToken';
    const ABI_buyToken = [
        {
            "inputs": [],
            "name": "buyToken",
            "outputs": [{ "internalType": "address", "name": "", "type": "address" }],
            "stateMutability": "payable",
            "type": "function"
        }];
    let options = {
        contractAddress: DEPLOYED_CONTRACT_ADDRESS,
        functionName: FUNCTION_NAME,
        abi: ABI_buyToken,
        msgValue: Moralis.Units.ETH(BNBValue)
    }
    console.log(`buyToken Contract Function options - `);
    console.log(options);
    console.log(`buyToken Contract Function options - msgVal`);
    console.log(options.msgValue);

    let result;

    /**
     * Get Coupon Code
     * If code exists && it's length is > 0 - means it was entreed
     * Check if it's valid 
     *      if valid - update in DB
     */
    let userEnteredCode = getUserEnteredCouponCode();
    if (userEnteredCode && userEnteredCode.length > 0) {
        try {
            let isValidCoupon = await isValidCouponCode(userEnteredCode);
            if (!isValidCoupon) {
                // throw error and return - means I need to do this checking before transaction is started
                toastSuccess(`❌Enter valid Coupon code or remove it`);
                return;
            }
        } catch (errCoupon) {
            console.error(errCoupon);
            console.error(`dev:buyToken() - failed to upload coupon user's detail`);
        }
    }
    try {
        // result = await Moralis.executeFunction(options);
        let userAddress = getUserWalletAddress();
        console.log(`Transaction started`);
        if (!TEST_ENVIRONMENT) swapAmountDataLayer(userAddress);
        const transaction = await Moralis.executeFunction(options);
        showTransactionLoader(true);
        if (!TEST_ENVIRONMENT) confirmTransactionDataLayer(userAddress);
        console.log(`Transaction in progress`);
        console.log(`Transaction`);
        console.log(transaction);

        result = await transaction.wait(CONFIRMATIONS_ON_BSC);
        console.log(`Transaction Hash`);
        console.log(result.transactionHash);
        updateBNBRaised();
        showTransactionLoader(false);


        if (typeof (toastSuccess) === 'undefined') {
            alert("✅Transaction Successful✅\nPlease Import token and check your wallet.");
        } else {
            toastSuccess("✅Transaction Successful.\nPlease Import token and check your wallet.");
        }


        let IBATValue = calculateIBATAmount(BNBValue);
        // the coupon validity is already check :P so just update it
        updateCouponUserDB(result.transactionHash, userAddress, BNBValue, IBATValue);
        refreshUsersInvestments();
        if (!TEST_ENVIRONMENT) {
            result && swapSuccessfulDataLayer(userAddress, result.transactionHash, BNBValue, IBATValue);
        }

        console.log(`Add: ${userAddress} \n tID: ${result.transactionHash} \n BNB: ${BNBValue} \nIBAT: ${IBATValue}`);

    } catch (error) {
        console.error(`Transaction cancelled`);
        if (error.message) {
            console.error(error.message);
            console.error(`dev:buyToken() - Transaction cancelled - 1`);
            msg = error.message;
            if (msg.includes('User denied transaction signature')) {
                msg = 'Transaction declined by User';
                showErrMsg(errMsgContainer, errMsg, msg);
                return;
            }
        }
        msg = error.data.message;
        console.error(msg);
        console.error(`dev:buyToken() - Transaction cancelled - 2`);
        msg = 'Insufficient BNB in wallet';
        showErrMsg(errMsgContainer, errMsg, msg);
    }

}

async function getTotalBNBAmount() {
    const functionName = 'totalBNBAmount';
    const ABI_totalBNBAmount = [{ "inputs": [], "name": "totalBNBAmount", "outputs": [{ "internalType": "uint256", "name": "", "type": "uint256" }], "stateMutability": "view", "type": "function" }];
    let options = {
        contractAddress: DEPLOYED_CONTRACT_ADDRESS,
        functionName: functionName,
        abi: ABI_totalBNBAmount,
    }

    let raisedAmount = await Moralis.executeFunction(options);
    raisedAmount = raisedAmount / Math.pow(10, DECIMALS);
    return raisedAmount;
}

async function populateAmountRaisedinHTML() {
    let BSC_Chain = TEST_ENVIRONMENT ? BscChainIdTest : BscChainIdMain;
    console.log(`populateAmountRaisedinHTML-switchAndAdd-chainId-${BSC_Chain}`);
    await switchAndAdd(BSC_Chain);

    console.log(`ran - populateAmountRaisedinHTML`);

    let raisedAmount = await getTotalBNBAmount(); //which will be 'logged out' if user is logged out
    raisedAmount = +raisedAmount.toFixed(2);
    console.log(`raisedAmount = ${raisedAmount}`);
    const HARDCAP = +16500;
    let raisedAmountPercentage = '';
    try {
        raisedAmountPercentage = percentageOfBNBRaised(raisedAmount, HARDCAP);
    } catch (error) {
        console.error(`dev: percentageOfBNBRaised() issue`);
    }
    // if (raisedAmountPercentage < 1) raisedAmountPercentage = 1;
    console.log(`raisedAmount = ${raisedAmount}`);
    console.log(`setting % = ${raisedAmountPercentage}`);
    // if user is logged-in we change the value of refferValue
    const progressBars = document.querySelectorAll('.sale-progress');


    progressBars && progressBars.forEach(el => {
        // in this example - the countValue = 0
        let percentageForBar = raisedAmountPercentage;
        percentageForBar < 1 ? percentageForBar = 1 : '';
        el.value = percentageForBar;
    });


    const raisedAmountHTML = document.querySelectorAll('.sale-progress-amount');
    raisedAmountHTML && raisedAmountHTML.forEach(el => {
        el.innerHTML = raisedAmount;
    })


    const raisedAmountPercentageHTML = document.querySelectorAll('.sale-progress-percentage');
    raisedAmountPercentageHTML && raisedAmountPercentageHTML.forEach(el => {
        el.innerHTML = raisedAmountPercentage;
    })


    const parentContainer = document.querySelectorAll('.sale-progress-val-parent');
    parentContainer.forEach(e => {
        let leftVal = +raisedAmountPercentage - 3;
        const maxLeftVal = +94;
        // console.log(`-left = ${leftVal}`);
        leftVal >= maxLeftVal ? leftVal = maxLeftVal : '';
        const minLeftVal = +0;
        leftVal <= minLeftVal ? leftVal = minLeftVal : '';
        // console.log(`-left = ${leftVal}`);
        // console.log(e);
        e.style.left = `${leftVal}%`;
    })
    const parentContainerMob = document.querySelectorAll('.sale-progress-val-parent-mob');
    parentContainerMob.forEach(e => {
        let leftVal = +raisedAmountPercentage - 3;
        const maxLeftValMob = +65;
        leftVal >= maxLeftValMob ? leftVal = maxLeftValMob : '';
        const minLeftValMob = +0;
        leftVal <= minLeftValMob ? leftVal = minLeftValMob : '';
        // console.log(`left = ${leftVal}`);
        // console.log(e);
        e.style.left = `${leftVal}%`;
    })
}

